import { mapState } from 'vuex'
export default {

	name: 'shoppingcart',

	props: {},

	data() {
		return {
			checked:true,
			value:1,
			rightText:'编辑',
			deletebutton:false,
			buttontext:'提交订单',
			DeleteFinished:false
		};
	},

	methods: {
		onSubmit(){
			if(this.buttontext=="删除"){
				let val=[];
				for(let i=0;i<this.ShoppingCart.length;i++){
					if(this.ShoppingCart[i].xzstate){
						val.push(this.ShoppingCart[i].goodschashu.GoodsTitle)
					}
				}

				this.deletegoods(val);
				return;
			}
			console.log('tijiao')
		},
		onClickRight(){
			this.rightText="完成";
			this.deletebutton=true;
			this.buttontext="删除";
			if(this.DeleteFinished){
				this.rightText="编辑";
				this.buttontext="提交订单";
			}

		},
		Total(){
			if(this.ShoppingCart.length){
				var val=[],test;
				for(var i=0;i<this.ShoppingCart.length;i++){
					if(this.ShoppingCart[i].xzstate){
						val.push(this.ShoppingCart[i].goodsNumber*this.ShoppingCart[i].goodschashu.Price)
						test=val.reduce((total, num)=>total+num)*100
					}
				}
				return test;
			}
		},
		deletegoods(val){
			this.$store.commit('updateShopping',val);
			console.log("输出了")
			this.DeleteFinished=true

		},
		
	},

	computed:{
		Totalselection:{
			get: function () {
				return this.ShoppingCart.every((val)=>val.xzstate);
			},
			set: function (newValue) {
				for(let i=0;i<this.ShoppingCart.length;i++){
					console.log(newValue)
					this.ShoppingCart[i].xzstate=newValue;
				}
			}
			
		},
		...mapState({
			ShoppingCart:"ShoppingCart"
		})},

//过滤器
filters: {},

mounted() {
	this.$nextTick(() => { })
},

//Vue 实例销毁后调用
destroyed() { },

components: {},

watch: {
}

};


